
        import {store, MERGED_BUNDLES_NAME} from 'bundleStore';
        
        let bundle;
        
        if ('jasperserver_config' !== MERGED_BUNDLES_NAME) {
            store['jasperserver_config'] = store['jasperserver_config'] || {};
            bundle = store['jasperserver_config'];
        } else {
            bundle = Object.keys(store).reduce((acc, key) => {
                return {
                    ...acc,
                    ...store[key]
                }
            }, {});
        }
                                     
        export default bundle;
    